import React, { useState, useEffect } from "react";
import "./IngredientsDesktop.scss";
import * as ScrollMagic from "scrollmagic";
import gsap from "gsap";
import KebabIngredients2 from "./images/baklazan-mieso-papryka-kukurydza.png";
import KebabIngredients3 from "./images/papryka-kukurydza.png";

const IngredientsDesktop = () => {
  // eslint-disable-next-line
  const [scrollMagic, setScrollMagic] = useState({
    controller: new ScrollMagic.Controller(),
    timelineOne: gsap.timeline(),
  });
  const { controller, timelineOne } = scrollMagic;
  useEffect(() => {
    // ingredients animation
    timelineOne.staggerFromTo(
      [".kebabIngredients2", ".kebabIngredients3"],
      0.5,
      { y: "50px", opacity: 0 },
      { y: "0", opacity: 1 },
      0.25
    );

    new ScrollMagic.Scene({
      triggerElement: ".ingredients_desktop2",
      triggerHook: 0.7,
    })
      .setTween(timelineOne)
      .addTo(controller);
    // eslint-disable-next-line
  }, []);
  return (
    <div className="ingredients_desktop2">
      <img
        src={KebabIngredients2}
        alt="Baklazan mieso papryka kukurydza"
        className="kebabIngredients2"
      />
      <img
        src={KebabIngredients3}
        alt="Papryka kukurydza"
        className="kebabIngredients3"
      />
    </div>
  );
};

export default IngredientsDesktop;
